.detailsContainer{
    margin: 130px 200px 90px 200px;
    background-color: rgba(88, 114, 228, 0.6);
    padding: 50px 40px;
    border-radius: 29px;
}
.profileImg{
    width: 300px;
}   
.rightContent .heading{
    position: absolute;
    left: 50%;
    top: 26%;
    font-size: 44px;
    font-family: 'IBM Plex Serif', serif;
}
.titleDetails{
    position: absolute;
    top: 36%;
    left: 50%;
    font-size: 22px;
    font-family: 'IBM Plex Serif', serif;
}
.unit, .mail{
    position: absolute;
    top: 43%;
    left: 50%;
    font-size: 18px;
    font-family: 'IBM Plex Serif', serif;
}
.mail{
    position: absolute;
    top: 49%;
}
.para{
    position: absolute;
    font-family: 'IBM Plex Serif', serif;
    top: 55%;
    left: 50%;
    width: 330px;
}
.contactDetails{
    position: absolute;
    font-family: 'IBM Plex Serif', serif;
    /* top: 77%; */
    margin-top: 1px;
    left: 50%;
}
.back{
    position: fixed;
    top: 12%;
    right: 10%;
    width: 100px;
}
.delete{
    position: fixed;
    top: 12%;
    right: 1%;
    width: 100px;
    background-color: rgba(194, 24, 24, 0.922);
}
.edit{
    position: fixed;
    top: 12%;
    right: 19%;
    width: 100px;
    background-color: green;
}
