.tbl-container{
    margin: 0px 10px;
    /* border: 1px solid black; */
    height: 575px;
    overflow: auto;
}
.id:hover{
    cursor: pointer;
    color: rgb(47, 203, 47);
}
.emp-heading{
    text-align: center;
    font-weight: 700;
    background-color: black;
    color: antiquewhite;
    position: fixed;
    width: 100%;
    padding: 10px 0;
}
table{
    margin-top: 80px;
}
.m-heading{
    font-family: 'IBM Plex Serif', serif;
    font-weight: 800;
}
.del{
    width: 30px;
}
.del:hover{
    cursor: pointer;
    transform: scale(1.08, 1.08);
}
.edit{
    width: 30px;
}
.edit:hover{
    cursor: pointer;
    transform: scale(1.08, 1.08);
}