  }
  .pre{
    margin-top: 6px;
    width: 40px;
    height: 40px;
    margin-right: 19px;
  }
  .next{
    margin-top: 6px;
    width: 40px;
    height: 40px;
  }
  .pre:hover{
    cursor: pointer;
    transform: scale(1.07);
  }
  .next:hover{
    cursor: pointer;
    transform: scale(1.07);
  }
  .add{
    position: absolute;
    top: 11.5%;
